
/* ------------------------------------web view */
.WebFoodCard-container{
    /* width: 30%; */
    margin: 5px 20px;
    display: flex;
    flex-direction: column;
}
.WebFoodCard-image{
    width: 180px;
    /* width: 80%; */
}
.WebFoodCard-name{
    margin-top: 5px;

    font-size: 16px;

}
.WebFoodCard-price{
    margin-top: 5px;

    font-size: 14px;
    color: #505050;
}

/* ------------------------------------mobile view */
.MobileFoodCard-container{
    width: 120px;
    margin: 5px auto;
    display: flex;
    flex-direction: column;
}
.MobileFoodCard-image{
    /* width: 120px; */
    width: 100%;
}
.MobileFoodCard-name{
    margin-top: 5px;

    font-size: 16px;

}
.MobileFoodCard-price{
    margin-top: 5px;

    font-size: 14px;
    color: #505050;
}