
/* ------------------------------------web view */
.WebIntroduction-container{
    width: 100%;
    margin-top: 20px;
    display: flex;
}
.WebIntroduction-logo{
    width: 20%;
    margin: auto 0;
}
.WebIntroduction-labels{
    margin: auto;
    margin-left: 5%;
    display: flex;
    flex-direction: column;

    align-items:flex-start;
}
.WebIntroduction-label-title{
    margin: 5px 0;
    
    font-family: cinzel;
    font-weight: bold;
    font-size: 20px;
}
.WebIntroduction-label-normal{
    margin: 2px 0;
    
    font-family: Cinzel;
    font-size: 16px;
}
.WebIntroduction-label-tel{
    margin: 20px 0;

    font-family: Cormorant Garamond;
    font-size: 26px;
    cursor: pointer;
}
.WebIntroduction-label-openingTime{
    margin: 1px 0;

    font-family: Cormorant Garamond;
    font-size: 15px;
    cursor: pointer;
}
.WebIntroduction-location{
    width: 40%;
    margin: auto 0;
}

/* ------------------------------------mobile view */
.MobileIntroduction-container{
    width: 100%;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
}
.MobileIntroduction-line{
    width: 100%;
    display: flex;
}
.MobileIntroduction-logo{
    width: 40%;
    margin: auto 0;
}
.MobileIntroduction-labels{
    margin: auto;
    margin-left: 10%;
    display: flex;
    flex-direction: column;

    align-items:flex-start;
}
.MobileIntroduction-label-title{
    margin: 5px 0;
    
    font-family: cinzel;
    font-weight: bold;
    font-size: 18px;
}
.MobileIntroduction-label-normal{
    margin: 2px 0;
    
    font-family: Cinzel;
    font-size: 14px;
}
.MobileIntroduction-label-tel{
    margin: 20px 0;

    font-family: Cormorant Garamond;
    font-size: 26px;
    cursor: pointer;
}
.MobileIntroduction-label-openingTime{
    margin: 1px 0;

    font-family: Cormorant Garamond;
    font-size: 13px;
    cursor: pointer;
}
.MobileIntroduction-location{
    width: 100%;
    margin: 10px auto;
}