
/* ------------------------------------web view */
.WebMenu-container{
    width: 100%;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
}
.WebMenu-title{
    width: 100%;

    text-align: center;
    font-family: Cormorant Garamond;
    font-size: 36px;
    font-weight: bold;
}
.WebMenu-divider{
    width: 100%;
    height: 2px;
    background-color: #707070;
}
.WebMenu-content{
    width: 100%;
    display: flex;
    margin: auto;
    margin-top: 5px;
}
.WebMenu-filter{
    width: 25%;
    margin: auto;
    margin-left: 0;
    margin-top: 0;
    display: flex;
    flex-direction: column;
}
.WebMenu-filterTitle{
    width: 100%;
    margin: 10px 0;
    
    text-align: center;
    font-family: Cormorant Garamond;
    font-size: 22px;
}
.WebMenu-categories{
    width: 80%;
    margin: 20px auto;
    display: flex;
    flex-direction: column;
}
.WebMenu-category-unselected{
    width: 100%;
    margin: 5px auto;
    padding: 2px;
    display: flex;
    background-color: #fff;
    cursor: pointer;

    font-family: Cormorant Garamond;
    font-size: 20px;
    color: #454545;

}
.WebMenu-category-selected{
    width: 100%;
    margin: 5px auto;
    padding: 2px;
    display: flex;
    background-color: #454545;
    cursor: pointer;
    
    font-family: Cormorant Garamond;
    font-size: 20px;
    color: #fff;
}
.WebMenu-itemList{
    width: 70%;
    margin-right: 0px;
    margin-top: 10px;
    display: flex;
    flex-wrap: wrap;

}

