/* ------------------------------------web view */
.Webheader-container{
    position: fixed;
    top: 0;

    height: 20%;
    width: 100%;
    display: flex;
    background-color: #000;
}

.Webheader-logo{
    width: 7%;
    margin: auto;
    margin-left: 40%;
}
.Webheader-labels{
    margin: auto;
    margin-left: 0px;
    display: flex;
    flex-direction: column;

    align-items:flex-end;
}
.Webheader-label-normal{
    margin: 5px 0;

    font-size: 16px;
    color: white;
}
.Webheader-label-button{
    margin: 5px 0;

    font-size: 16px;
    color: white;
    cursor: pointer;
}

/* ------------------------------------mobile view */
.Mobileheader-container{
    position: fixed;
    top: 0;

    width: 100%;
    height: 15%;
    display: flex;
    background-color: #000;
}
.Mobileheader-logo{
    width: 20%;
    margin: auto;
}
.Mobileheader-labels{
    margin: auto;
    margin-left: 0px;
    display: flex;
    flex-direction: column;

    align-items:flex-end;
}
.Mobileheader-label-normal{
    margin: 3px 0;

    font-size: 16px;
    color: white;
}
.Mobileheader-label-button{
    margin: 3px 0;

    font-size: 14px;
    color: white;
    cursor: pointer;
}